                Jméno pacienta:
                <input type="text" style="margin-left:10px" v-on:input="patientChange($event.target.value)" />
            </div>
            <div class="row">
                Rodné číslo:
                <input type="text" style="margin-left:10px" v-on:input="rcChange($event.target.value)" />
            </div>
            <div class="row">
                Pojišťovna:
                <input type="text" style="margin-left:10px" v-on:input="pojChange($event.target.value)" />
            </div>
            <div class="row">
                Počet procedur:
                <input type="text" style="margin-left:10px" v-on:input="procedureCountChange($event.target.value)" />
            </div>
            <div class="row">
                Datum od:
                <input type="text" style="margin-left:10px" v-on:input="dateFromChange($event.target.value)" />
            </div>
            <div class="row">
                Dny:
                <input type="text" style="margin-left:10px" v-on:input="dayChange($event.target.value)" />
            </div>
            <div class="row">
                Čas:
                <input type="text" style="margin-left:10px" v-on:input="appointmentChange($event.target.value)" />
            </div>
            <div class="row">
                místo aplikace:
                <input type="text" style="margin-left:10px" v-on:input="applicationChange($event.target.value)" />
            </div>
            <div class="row">
                velikost hlavice:
                <label for="uz1"> 1cm<sup>2</sup> </label>
                <input type="radio" id="uz1" value="1" v-model="uzHead" />
                <label for="uz2"> 4cm<sup>2</sup> </label>
                <input type="radio" id="uz4" value="4" v-model="uzHead" />

            </div>
            <div class="row">
                doba aplikace:
                <input type="text" style="margin-left:10px" v-on:input="timeChange($event.target.value)" />
            </div>
            <div class="row">
                intensita:
                <input type="text" style="margin-left:10px" v-on:input="intensityChange($event.target.value)" />
            </div>
            <hr style="margin-bottom: 20%;" />
        </div>
        <div class="main_form">
            <clinic-header></clinic-header>
            <div class="main_body">
                {{procedureName}}
                <GeneralInfo v-bind:data="data" v-bind:procedureName="procedureName" v-bind:cabin="cabin"></GeneralInfo>
                <div class="blank"></div>

                <table class="table" style="page-break-after:always">
                    <tr>

                        <th class="table_first_row init"><b>datum </b></th>
                        <th class="table_first_row middle"><b>popis</b></th>
                        <th class="table_first_row last"><b>razítko a podpis</b></th>
                    </tr>

                    <DataRow v-bind:data="data" v-bind:cabin="cabin" v-bind:head="uzHead" />
                    <DataRow v-bind:data="data" v-bind:cabin="cabin" v-bind:head="uzHead" />
                    <DataRow v-bind:data="data" v-bind:cabin="cabin" v-bind:head="uzHead" />
                    <DataRow v-bind:data="data" v-bind:cabin="cabin" v-bind:head="uzHead" />
                </table>
                <table class="table">
                    <tr>
                        <th class="table_first_row init"><b>datum </b></th>
                        <th class="table_first_row middle"><b>popis</b></th>
                        <th class="table_first_row last"><b>razítko a podpis</b></th>

                    </tr>
                    <DataRow v-bind:data="data" v-bind:cabin="cabin" v-bind:head="uzHead" />
                    <DataRow v-bind:data="data" v-bind:cabin="cabin" v-bind:head="uzHead" />
                    <DataRow v-bind:data="data" v-bind:cabin="cabin" v-bind:head="uzHead" />
                    <DataRow v-bind:data="data" v-bind:cabin="cabin" v-bind:head="uzHead" />
                    <DataRow v-bind:data="data" v-bind:cabin="cabin" v-bind:head="uzHead" />
                    <DataRow v-bind:data="data" v-bind:cabin="cabin" v-bind:head="uzHead" />
                </table>

            </div>
        </div>
    </div>


  
  
</template>

<script>
    import DataRow from "@/components/DataRow_ultrazvuk";
    import GeneralInfo from "@/components/GeneralInfo";
    import ClinicHeader from "@/components/Header"

export default {
        name: 'uz',
        components: {
            DataRow,
            ClinicHeader,
            GeneralInfo
        },
  props: {
      msg: String,
      procedureName: String,
      cabin: String,
        },
        data: function () {
            return {
                uzHead: "",
                data: {
                    time: "",
                    application: "",
                    electrodes: "",
                    intensity: "",
                    patientName: "",
                    program: "",
                    rc: "",
                    poj: "",
                    procedureCount: "",
                    date: "",
                    day: "",
                    appointment: "",
                },
                update: 0

            }           
        },
        methods: {
            procedureCountChange: function (val) {
                this.data.procedureCount = val;
            },
            dateFromChange: function (val) {
                this.data.date = val;
            },
            dayChange: function (val) {
                this.data.day = val;
            },
            appointmentChange: function (val) {
                this.data.appointment = val;
            },
            applicationChange: function (val) {
                this.data.application = val
            },
            electrodeChange: function (val) {
                this.data.electrodes = val
            },
            intensityChange: function (val) {
                this.data.intensity = val
            },
            timeChange: function (val) {
                this.data.time = val
            },
            patientChange: function (val) {
                this.data.patientName = val;
            },
            rcChange: function (val) {
                this.data.rc = val;
            },
            pojChange: function (val) {
                this.data.poj = val;
            },
            programChange: function (val) {
                this.data.program = val;
            }
        }
}
</script>

